
import { defineComponent, ref, reactive, onMounted, watch } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  setup() {
    const store = useStore();
    const loginFormRef = ref();
    const isDisabled = ref(true);
    const loginForm = reactive({
      name: 'rainbow',
      pwd: '1qaz2wsx',
    });

    const loginRules = {
      name: [
        { required: true, message: '請輸入帳號', trigger: 'blur' },
        { min: 3, max: 15, message: '長度在3-15之間', trigger: 'blur' },
      ],
      pwd: [{ required: true, message: '請輸入密碼', trigger: 'blur' }],
    };

    const formValidate = () => {
      loginFormRef.value.validate(
        (valid: boolean) => (isDisabled.value = !valid)
      );
    };

    onMounted(() => {
      formValidate();
    });

    watch(loginForm, () => formValidate(), { deep: true });

    const onSubmit = async () => {
      if (isDisabled.value) {
        return;
      } else {
        store.dispatch('handLogin', { data: loginForm });
      }
    };

    return {
      loginFormRef,
      loginForm,
      loginRules,
      isDisabled,
      onSubmit,
    };
  },
});
